.menu-btn {
    position: absolute;
    z-index: 3;
    right: 35px;
    top: 35px;
    cursor: pointer;
    @include easeOut;

    .btn-line {
        width: 28px;
        height: 3px;
        margin: 0 0 5px 0;
        background: #fff;
        @include easeOut;
    }
    .btn-line-last {
        width: 20px;
        height: 3px;
        margin: 0 0 5px 0;
        background: #fff;
        @include easeOut;
    }

    //Rotate to X with Menu Lines
    &.close {
        transform: rotate(180deg);

        .btn-line {
            &:nth-child(1){
                transform: rotate(45deg) translate(5px, 5px);
            }
            &:nth-child(2){
                opacity: 0;
            } 
        }
        .btn-line-last {
            width: 28px;
            transform: rotate(-45deg) translate(7px, -6px);
        }
    }
}